form{
    display: flex;
    flex-direction: column;
}
form>*{
    margin: 5px;
}
.form-item{
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.form-item> input, .form-item > select{
    flex: 1;
    padding: 4px;
    height: 25px;
    font-size: 12px;
    box-sizing: border-box;
    border:none;
    border-radius: 0 12px 12px 0;
    border: thin solid #3f51b5;
    outline: none;
}
.form-item > label{
    min-width: 200px;
    font-size: 12px;
    box-sizing: border-box;
    border-radius:  12px 0 0 12px ;
    background-color: #3f51b5;
    color: aliceblue;
    padding: 5px;
}

.file-item{
    position: relative;
    border-radius: 12px;
}
.file-item> input{
    border: none;
}
.file-item > label{
    position: absolute;
    width:100%;
    height: 100%;
    background-color: #eee;
    border: thin dashed #3f51b5;
    border-radius: 12px;
    cursor: pointer; 
    color: black;
}
.file-item > label:hover{
    background-color: #cccccc;
}
button{
    background-color: #3f51b5;
    color: white;
    border: none;
    padding: 6px;
    margin:10px;
    border-radius: 12px;
}